<template>
  <div class="main-wrapper">
    
    <div class="main-page">
      <TextContent
        v-if="infoText"
        :text="infoText"
        class="text-section"
      />
      <TextContent
        v-if="questionText"
        :text="questionText"
        class="text-section"
      />

      <div
        v-if="answerTransitions.length > 1"
        class="answer-section"
      >
        <div
          v-for="answer, index in answerTransitions"
          :key="index"
          class="answer-item"
        >
          <input type="radio" :id="index" :value="answer.transitionId" v-model="pickedTransitionId" />
          <label :for="index">{{ answer.answerText }}</label>
        </div>
      </div>

      <!-- <FooterButtons
        class="footer-section"
      /> -->

      <div class="footer-section">
        
        <button
          v-if="questionStack.length > 1"
          @click="previousQuestionAndClearPicked"
        >
          Назад
        </button>
        <button
          v-if="answerTransitions.length > 0"
          @click="nextQuestionAndClearPicked"
        >
          Далее
        </button>
      
      </div>

    </div>

    <ContinueModal
      v-if="showContinueModal"
      @checkContinueTest = checkContinueTest($event)
    />

  </div>
</template>

<script>
import { mapActions, mapGetters} from 'vuex'
import TextContent from '@/components/TextContent.vue'
import ContinueModal from '@/components/ContinueModal.vue'
// import { is } from '@babel/types'

export default {
    data() {
      return {
        pickedTransitionId: "",
        showContinueModal: false,
        questionListFromLocalStorage: []
      }
    },
    components:{
        TextContent,
        ContinueModal
    },
    computed: {
      ...mapGetters([
        "testId",
        "questionId",
        "parentId",
        "infoText",
        "questionText",
        "answerTransitions",
        "questionStack",
        "startQuestionId"
      ])
      
    },
    methods: {
      ...mapActions([
        "GET_QUESTION_DATA",
        "setCurrentTransitionId",
        "nextQuestion",
        "previousQuestion",
        "continueTest",
        "newTest"
      ]),
      previousQuestionAndClearPicked(){
        this.pickedTransitionId = ""
        this.previousQuestion()
      },
      nextQuestionAndClearPicked(){
        this.pickedTransitionId = ""
        this.nextQuestion()
      },

      checkLocalStorage(){
        const currentQuestionListRow = localStorage.getItem(`questionList${this.testId}`)
        if (currentQuestionListRow) {
          const currentQuestionList = JSON.parse(currentQuestionListRow)

          if (currentQuestionList.length > 1) {
            console.log(currentQuestionList);
            this.questionListFromLocalStorage = currentQuestionList
            this.openContinueModal()
          }
          else{
            this.createLocalStorage()
          }
        }
        else {          
          this.createLocalStorage()
        }
      },
      createLocalStorage(){
        localStorage.setItem(`questionList${this.testId}`, JSON.stringify([this.startQuestionId]))
        this.GET_QUESTION_DATA()
      },
      openContinueModal(){
        this.showContinueModal = true
      },
      checkContinueTest(isContinue){
        this.showContinueModal = false
        if (isContinue) {
          this.continueTest(this.questionListFromLocalStorage)
        }
        else {
          this.newTest()
        }
      }

    },
    mounted() {
        this.checkLocalStorage()
        // this.GET_QUESTION_DATA()
        this.pickedTransitionId = ""
    },
    watch: {
      pickedTransitionId(newTransitionId){
      this.setCurrentTransitionId(newTransitionId)
    }
  }
}

</script>

<style>
  .main-wrapper{
    display: flex;
    justify-content: center;
  }
  .main-page{
    width: 800px;
    margin-top: 100px;
    border: 1px solid black;
    padding: 20px;
  }
  .text-section{
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .answer-section{
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .answer-item{
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .footer-section{
    margin-top: 20px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>