<template>
  <div class="continue-wrapper">
    <div class="continue-page">
      <div>
        Продолжить?
      </div>
      <div class="button-list">
        <button
          @click="checkContinueTest(true)"
        >Продолжить</button>
        <button
          @click="checkContinueTest(false)"
        >Начать с начала</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    methods: {
      checkContinueTest(isContinue){
        this.$emit('checkContinueTest', isContinue)
      }
    },
  }
</script>

<style>
  .continue-wrapper{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .continue-page{
    width: 300px;
    height: 200px;
    display: flex;
    flex-direction: column;
    background: #96faa7;
  }
  .button-list{
    display: flex;
    justify-content: center;
  }

</style>